import React from "react";
import s from "./login.module.css";
import {reduxForm } from "redux-form";
import { requiredField, emailValidation, maxLengthCreator } from "../../common/validators/validators";
import {inputFormControl, fieldCreator } from "../../common/FormControls/FormsControl";

const maxLength15 = maxLengthCreator(15)

const LoginForm = ({handleSubmit,error}) => {
    return(
            <form onSubmit={handleSubmit}>
                {fieldCreator(inputFormControl,[requiredField, emailValidation],"email","text","Login",null)}
                {fieldCreator(inputFormControl,[requiredField, maxLength15],"password","password","Password",null)}
                {fieldCreator(inputFormControl,null,"rememberMe","checkbox",null,"Remember Me")}
                {error && <div>{error}</div>}
                <div className={s.butDiv}>
                    <button className={s.but}>LOGIN</button>
                </div>
                <div>
                    <h4>If you are not registered, do it <a href="https://social-network.samuraijs.com/signUp">HERE</a></h4> 
                </div>
            </form>
    );
}

const LoginReduxForm = reduxForm({form: "login"})(LoginForm)

const Login = ({loginThunkCreator}) => {

    const onSubmit = (formData) => {
        loginThunkCreator(formData);
    }

    return(
        <div className={s.place}>
            <h3 className={s.reg}>Login to join</h3>
            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    );
}

export default Login;